  callWSToReloadPagesData() {

    this.arrOutputAccumPack = [];
    this.arrOutputNonAccumPack = [];
    this.arrOutputAccumNonPack = [];
    this.arrOutputNonAccumNonPack = [];

    let response1 = this.service.callWebService(Constants.k_HOURLY_OUTPUT, this.outputAccumPackReqModel, res => {
      if (res && res.data && res.data != '' && res.data != '-') {
        this.arrOutputAccumPack = JSON.parse(res.data);
      }
    });
    let response2 = this.service.callWebService(Constants.k_HOURLY_OUTPUT, this.outputNonAccumPackReqModel, res => {
      if (res && res.data && res.data != '' && res.data != '-') {
        this.arrOutputNonAccumPack = JSON.parse(res.data);
      }
    });
    let response3 = this.service.callWebService(Constants.k_HOURLY_OUTPUT, this.outputAccumNonPackReqModel, res => {
      if (res && res.data && res.data != '' && res.data != '-') {
        this.arrOutputAccumNonPack = JSON.parse(res.data);
      }
    });
    let response4 = this.service.callWebService(Constants.k_HOURLY_OUTPUT, this.outputNonAccumNonPackReqModel, res => {
      if (res && res.data && res.data != '' && res.data != '-') {
        this.arrOutputNonAccumNonPack = JSON.parse(res.data);
      }
    });

    this.service.callWebServiceSimultaneously([response1, response2, response3, response4]);

  }

