  random_rgb() {
    var o = Math.round, r = Math.random, s = 255;
    return 'rgb(' + o(r() * s) + ',' + o(r() * s) + ',' + o(r() * s) + ',' + r().toFixed(1) + ')';
  }

  standardDateTime(dateString: string) {
    return moment(dateString).format("YYYY-MM-DD");
  }

  async presentModal() {
    const modal = await this.modalController.create({
      component: SelectMachineComponent,
      componentProps: {
        "checkBoxList": this.checkBoxList,
        "paramTitle": "Select Department"
      }
    });

    modal.onDidDismiss().then((dataReturned) => {
      if (dataReturned !== null && dataReturned.data !== undefined) {
        // this.dataReturned = dataReturned.data;

        this.wipReqModel.dept = [];

        dataReturned.data.forEach(element => {
          if (element.isChecked) {
            this.wipReqModel.dept.push(element.machine.value)
          }
        });
        console.log('--- Modal SELECTED Data: ', this.wipReqModel.dept);

        this.optionsFn();

      }
    });

    return await modal.present();
  }

  downloadcsv() {

    this.downloadCSVReqModel.packaging = this.isPack ? '1' : '0';
    this.downloadCSVReqModel.accumulated = this.isAccum ? '1' : '0';

    BasePagePage.presentAlert('Extract Data', 'Are you sure want to extract data as file to your local storage?', (res) => {

      this.downloadCSVReqModel.date = this.wipReqModel.date;

      this.service.presentLoading();

      this.service.callWebService(Constants.k_DOWNLOAD_CSV, this.downloadCSVReqModel, (res) => {

        if (res && res.data && res.data != '' && res.data != '-') {

          let arr = JSON.parse(res.data);
          let filepath = arr[0];
          let fullpath = /*Constants.k_ROOT_DOMAIN_URL + "/" +*/ filepath.filepath
          window.open(fullpath, '_system');

        }
        
        this.service.dismissLoading();

      });

    }, [Constants.b_CANCEL, Constants.b_OK]/*, [{
      name: 'email',
      type: 'email',
      placeholder: 'Recipient email here'
    }]*/);

  }

